.container {
  --color: #239ce2; /* Default theme color */
  --font-family: Arial, sans-serif; /* Default font */
  font-family: var(--font-family);
  min-width: 700px;
  max-width: 850px;
  margin: 0 auto;
  flex: 1.2;
  height: fit-content;
  min-height: 900px;
  box-shadow: 1px 1px 3px 2px rgba(0, 0, 0, 0.1);
  display: flex;
  flex-direction: column;
  gap: 30px;
  padding: 30px;
}

/* Styling for the control buttons (font and color selectors) */
.controls {
  display: flex;
  gap: 20px;
  justify-content: center;
  margin-bottom: 20px;
}

.controls label {
  display: flex;
  align-items: center;
  gap: 10px;
  font-weight: bold;
  font-size: 1rem;
}

.controls input[type="color"],
.controls select {
  padding: 8px 15px;
  border: 1px solid #ccc;
  border-radius: 5px;
  background-color: var(--color);
  font-weight: bold;
  cursor: pointer;
  transition: background-color 0.3s ease;
}



/* Style for the download button, assuming you have one */
.downloadButton {
  padding: 10px 20px;
  background-color: var(--color);
  color: white;
  font-weight: bold;
  text-align: center;
  border-radius: 5px;
  cursor: pointer;
  border: none;
  transition: background-color 0.3s ease;
}

.downloadButton:hover {
  background-color: #1e87c9;
}



.header {
  display: flex;
  flex-direction: column;
}

.header .heading {
  font-size: 2.7rem;
  font-weight: 500;
  text-transform: capitalize;
}

.header .subHeading {
  color: var(--color); /* Dynamic theme color applied */
  font-weight: 500;
  font-size: 1.1rem;
}

.header .links {
  margin-top: 15px;
  display: flex;
  gap: 30px;
  flex-wrap: wrap;
  row-gap: 10px;
}

.header .link {
  font-size: 0.875rem;
  display: flex;
  align-items: center;
  gap: 5px;
  cursor: pointer;
}

.header .link svg {
  color: var(--color); /* Dynamic theme color applied */
  height: 16px;
  width: 16px;
}

.main {
  display: flex;
  gap: 30px;
}

.col1, .col2 {
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.col1 {
  flex: 1.3;
}

.col2 {
  flex: 1;
}

.section .sectionTitle {
  font-size: 1.4rem;
  font-weight: bold;
  width: 100%;
  border-bottom: 2px solid #000;
}

.section .content {
  display: flex;
  flex-direction: column;
  gap: 10px;
  padding: 10px 0;
}

.section .content .item {
  border-bottom: 1px dotted lightgray;
  display: flex;
  flex-direction: column;
  gap: 4px;
  padding-bottom: 10px;
}

.section .content .item:last-child {
  border-bottom: none;
}

.content .title {
  font-weight: 500;
  font-size: 1rem;
  line-height: 1.3rem;
}

.content .subTitle {
  font-weight: bold;
  color: var(--color); /* Dynamic theme color applied */
  font-size: 1rem;
}

.content .overview {
  font-size: 0.875rem;
}

.content .link {
  display: flex;
  gap: 5px;
  font-size: 0.75rem;
  cursor: pointer;
  color: var(--color); /* Dynamic theme color applied */
}

.content .link svg {
  height: 14px;
  width: 14px;
  color: var(--color); /* Dynamic theme color applied */
}

.content .date {
  display: flex;
  gap: 5px;
  align-items: center;
  font-size: 0.875rem;
}

.content .date svg {
  height: 14px;
  width: 14px;
}

.content .points, .numbered {
  padding-left: 18px;
  font-size: 0.875rem;
}

.numbered li {
  list-style-type: decimal;
}

.hidden {
  display: none;
}
  